html {
  background: #fafafa url("../img/background.jpg");
  background-size: cover;
  font-family: 'helvetica neue';
  text-align: center;
  font-size: 10px;
}

body {
  margin: 0;
  font-size: 2rem;
  display: flex;
  flex: 1;
  min-height: 100vh;
  align-items: center;
}

.clock {
  background: #313131;
  width: 30rem;
  height: 30rem;
  border: 20px solid #252525;
  border-radius: 50%;
  margin: 50px auto;
  position: relative;
  padding: 2rem;
  box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.1), inset 0 0 0 3px #313131, inset 0 0 10px black, 0 0 10px rgba(0, 0, 0, 0.2);
}
.clock__face {
  position: relative;
  width: 100%;
  height: 100%;
  transform: translateY(-3px);
  /* account for the height of the clock hands */
}

.hand {
  width: 50%;
  height: 6px;
  background: #000;
  position: absolute;
  top: 50%;
  transform-origin: 100%;
  transform: rotate(90deg);
  transition: all 0.5;
  transition-timing-function: cubic-bezier(0.1, 2.7, 0.58, 1);
}
.hand__minute {
  width: 35%;
  left: 15%;
}
.hand__second {
  height: 3px;
  width: 45%;
  left: 5%;
  background: #DE243B;
}

